import React from "react";
import { Button, Text } from "@pancakeswap-libs/uikit";
import ConnectWalletButton from "components/ConnectWalletButton";

export default function ClaimButton({
  live,
  claimed,
  pendingTx,
  account,
  chainId,
  claimableAmount,
  onClaim,
}) {
    console.log("pendingTx", pendingTx);
  if (!account) {
    return <ConnectWalletButton width="100%" />;
  }
  let errorMessage = "";
  let hint = "";
  if (!live) {
    errorMessage = "Airdrop Not Live Yet";
  }
  if (!claimableAmount) {
    errorMessage = "No Claimable Amount";
    hint = "Ensure your connected wallet address is correct";
  }
  if (pendingTx) {
    errorMessage = "Pending";
  }
  if (!claimableAmount) {
    errorMessage = "No Claimable Amount";
    hint = "Ensure your connected wallet address is correct";
  }
  if (chainId !== 5 && chainId !== 1) {
    errorMessage = "Connect to Ethereum Network";
    hint = `Currently connected to ${chainId}`;
  }
  if (claimed) {
    errorMessage = "Airdrop Claimed";
    hint = '';
  }
  return (
    <>
      <Button
        disabled={!!errorMessage}
        onClick={onClaim}
        id="harvest-all"
        width="100%"
      >
        {errorMessage || "Claim Airdrop"}
      </Button>
      <Text color="textDisabled" fontSize="12px" style={{ lineHeight: "16px" }}>
        {hint}
      </Text>
    </>
  );
}
