import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { Address } from "config/constants/types";
import { getAddress } from "utils/addressHelpers";
import { getFullDisplayBalance } from "utils/formatBalance";
import { useContract } from "hooks/useContract";
import BoxLogo from "assets/images/boxlogo.png";
import IbitCoin from "assets/images/ibitCoin.png";
import { useToast } from "state/hooks";
import { Heading, CardBody, Button, Text } from "@pancakeswap-libs/uikit";
import { Link } from "react-router-dom";
import { useWeb3React } from "@web3-react/core";
import useI18n from "hooks/useI18n";
// import { useAllHarvest } from "hooks/useHarvest";
// import useFarmsWithBalance from "hooks/useFarmsWithBalance";
import Card from "components/custom/Card";

import AirdropVault from "./abi.json";
import Whitelist from "./whitelist.json";
import CardValue from "../CardValue";
import ClaimButton from "./ClaimButton";

const StyledFarmStakingCard = styled(Card)`
  background-repeat: no-repeat;
  background-size: 30%;
  background-position: 80% 50%;
  min-height: 376px;
  max-width: 600px;
  transform: translate(0px, 30px);
`;

const Block = styled.div`
  margin-bottom: 16px;
`;

const CardImage = styled.img`
  margin-bottom: 16px;
  margin-left: 10px;
`;

const Label = styled.div`
  color: ${({ theme }) => theme.colors.textSubtle};
  font-size: 14px;
`;

const Actions = styled.div`
  margin-top: 24px;
`;

const AirdropVaultAddress: Address = {
  1: "0x6FfdB87A2BC0EaD7Ddb070c8112A1421C0fd3979",
  5: "0x083FEd9c3A2AB4d2541c95652d2068A8a471716f",
};

const FarmedStakingCard = () => {
  const [pendingTx, setPendingTx] = useState(false);
  const [live, setLive] = useState(false);
  const [claimed, setClaimed] = useState(false);
  const { toastSuccess, toastError } = useToast();

  const { account, chainId } = useWeb3React();
  const TranslateString = useI18n();

  const airdropVaultAddress =
    AirdropVaultAddress[chainId] || getAddress(AirdropVaultAddress);
  const airdropContract = useContract(AirdropVault, airdropVaultAddress);

  useEffect(() => {
    const fetchData = async () => {
      // if account, See if already claimed
      if (account) {
        const isLive = await airdropContract.methods.isAirdropLive().call();
        const airdropped = await airdropContract.methods
          .airdropped(account)
          .call();
        setLive(isLive);
        setClaimed(airdropped);
        console.log("isLive", isLive);
      }
    };
    fetchData();
  }, [account, airdropContract]);

  const handleClaim = async () => {
    setPendingTx(true);
    try {
      const { totalAmount, messageHash, v, r, s } =
        Whitelist[account?.toLowerCase()] || {};

      const bufR = Buffer.from(r);
      const bufS = Buffer.from(s);

      await airdropContract.methods
        .claim(totalAmount, messageHash, v, bufR, bufS)
        .send({ from: account });
      setClaimed(true);
      toastSuccess("Successfully claimed!");
    } catch (error : any) {
      console.log("error", error);
      toastError(
        error?.name || "Error", // @ts-ignore
        error?.message || "Something went wrong",
      );
    } finally {
      setPendingTx(false);
    }
  };

  const claimableAmount = claimed
    ? 0
    : Whitelist[account?.toLowerCase()]?.totalAmount || 0;
  const formattedClaimAmount = (claimableAmount / 1e18).toFixed(0);

  return (
    <StyledFarmStakingCard>
      <CardBody style={{ textAlign: "center" }}>
        <Heading size="xl" mb="24px">
          {TranslateString(542, "Airdrop")}
        </Heading>
        <CardImage src={IbitCoin} alt="logo" width={84} height={84} />
        <Block>
          <Label>Your Claimable Airdrop:</Label>
          {account ? (
            <CardValue
              value={Number(formattedClaimAmount)}
              decimals={0}
              lineHeight="1.5"
            />
          ) : (
            <Text color="textDisabled" style={{ lineHeight: "76px" }}>
              Connect wallet to view
            </Text>
          )}
        </Block>
        <Block>
          <Label>Connected Wallet Address:</Label>
          {account ? (
            <Text fontSize="10px" lineHeight="36px">
              {account}
            </Text>
          ) : (
            <Text color="textDisabled" style={{ lineHeight: "76px" }}>
              Connect wallet to view
            </Text>
          )}
        </Block>
        <Actions>
          <ClaimButton
            live={live}
            claimed={claimed}
            pendingTx={pendingTx}
            account={account}
            chainId={chainId}
            claimableAmount={claimableAmount}
            onClaim={handleClaim}
          />
        </Actions>
      </CardBody>
    </StyledFarmStakingCard>
  );
};

export default FarmedStakingCard;
